const Svg = SvgIcon_Cmpt();
const DropInput = SelectDropAccount_Cmpt();



const GlobalAccount  = ({ data, keyId  }) => {
	const [obs, set_obs] = useState(0);


	const updObs = () =>{
		set_obs(gen16CodeId())
	}



	useEffect(() => {
		get_balances_by_type("global_accounts","global_account_params",updObs);
	},[keyId]);
	


	
	let flds = [
		"inventory",
		"inventory_expensess",
		"sales_income",
		"cash_bank",
		"advance_collections",
		"sale_tax",
		"socialsecurity_tax_expenses",
		"medicare_tax_expenses",
		"salary",
		"futureSlipCheck",
		"w2_params"
	]





	const save = async () => {
		if(getStore("global_account_params")?.balanceId){
			let frm2Sv = {...getStore("global_account_params")};
			frm2Sv.balanceId = gen16CodeId();
			let bdyq2 = {...getStore(qryStore())?.upd_balances};
			bdyq2.params = {
				businessId:  getStore("profileUser")?.businessId,
				balanceId: getStore("global_account_params")?.balanceId,
			};

			let diff = compareJSON(flds,getStore("global_account_params"),getStore("global_account_params" + "_bck") )
			
			bdyq2.data2update=diff.data;
			

			const res = fetchEncryptStream(bdyq2);
			const td = await res;
			if (td) {
				updStore("global_account_params", frm2Sv);
			}
		}
		else{
			let frm2Sv = {...getStore("global_account_params")};
			frm2Sv.balanceId = gen16CodeId();
			frm2Sv.type = "global_accounts";
			let bdyq2 = {...getStore(qryStore())?.add_balances};
			bdyq2.params = {
				businessId:  getStore("profileUser")?.businessId
			};
			bdyq2.form=frm2Sv;
			const res = fetchEncryptStream(bdyq2);
			const td = await res;
			if (td) {
				updStore("global_account_params", frm2Sv);
				set_obs(gen16CodeId())
			}
		}
		data?.modalID && CloseModal({id:data?.modalID});
	}

	// console.log(JSON.stringify(getStore("global_account_params"),null,2))
	
	
	

	return (
		
		<div style={{
			maxHeight: "80vh",
			width: "690px",
			overflow: "auto",
			background: "#fff",
			borderRadius: 13,
			padding: "10px 19px"
		}}>
			<div className="_dsplFlx ">
				<p className="title_g_acc" >
					Cuentas Globales
				</p>
				<div className="flexSpace"/>
			</div>
			<div className="balance" style={{marginTop: 10, minHeight: 20}}>
			</div>
			<CardInventoryAcc item_global={getStore("global_account_params")}/>
			<div style={{marginTop: 10}}/>
			<PayrollAccountsDash item_global={getStore("global_account_params")}/>
			<div style={{marginTop: 10}}/>
			<CardTaxW2  item={getStore("global_account_params").w2_params}/>
			<div className="_dsplFlx ">
				<div className="flexSpace"/>
				<div className="card">
					<button onClick={save}>
						Guardar
					</button>
				</div>
			</div>
		</div>
	)
}



export default GlobalAccount;




const DetailAccount  = ({ keyId, item }) => {
	const [edit, setEdit] = useState(false);
	const [show, setShow] = useState(false);
	const [obs, set_obs] = useState(0);
	let accountId = getStore("global_account_params")?.[keyId];

	let data = getStore("account_ids")?.[accountId];
	let subAcc = data && getStore("sub_account_group")?.[data?.accountId];




	
	const updFrm = (f, v, k) => {
		let frmNm = "global_account_params"
		let frm2 = getStore(frmNm) || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore(frmNm, frm2);
		
		set_obs(gen16CodeId())
	};

	const selectItem = (v) => {
		updFrm(keyId, v);
		setEdit(false)
	}


	return (
		<div style={{margin: "7px 0 7px 27px"}}>
			<div  className="_dsplFlx " >
			<div 
				onClick={()=>setEdit(!edit)} 
				className={`${edit?"editing":""}`}  
				style={{marginRight: 27, minWidth: 160}}
			>
				<div className={"acc_global_lbl"}>
				{item.lbl} 
				</div>
			</div>
			{edit?
			<div>
				<SelectAccounts
					handleClick={selectItem}
				/>
			</div>
			:
			<div  className="_dsplFlx ">
				<div className={"bg_acc_code"}>
					{data?.code}
				</div>
				
				<span className={"bg_acc_amount"} style={{marginLeft: 13}}>
					{data?.name}
				</span>
			</div>
			
			}
			<div className="flexSpace"/>
			
		</div>
		
	</div>
	)
}












export const CardTaxW2  = ({ item, item_global }) => {
	const [obs, set_obs] = useState(0);

	
	const updFrm = (f, v, k) => {
		let frmNm = "global_account_params"
		let frm2 = getStore(frmNm) || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore(frmNm, frm2);
		set_obs(gen16CodeId())
	};
	
	const updFrmTx = (f, v, k) => {
		let frm2 = item || defaultW2;
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		
		updFrm("w2_params", frm2);
		set_obs(gen16CodeId())
	};

 
	return (
		<CardsHrm >
			<div className="iMTYiw hiKoiM">{"W2 Tax deduction Parameter"}</div>
			<div className="_dsplFlx w_100prc" style={{marginTop:19}} />
			
			<TaxDecuctionItem 
				label={"Federal Tax"}
				percentDefault={14.5}
				onChange={updFrmTx}
				keyFld={"federalTax"}
				item={item}
			/>
			<TaxDecuctionItem 
				label={"Social Security"}
				percentDefault={6.2}
				onChange={updFrmTx}
				keyFld={"socialSecurity"}
				item={item}
			/>
			<TaxDecuctionItem 
				label={"Medicare"}
				percentDefault={1.45}
				onChange={updFrmTx}
				keyFld={"medicareTax"}
				item={item}
			/>
			<TaxDecuctionItem 
				label={"Estate"}
				percentDefault={4.5}
				onChange={updFrmTx}
				keyFld={"stateTax"}
				item={item}
			/>

			<TaxDecuctionItem 
				label={"City"}
				percentDefault={2.2}
				onChange={updFrmTx}
				keyFld={"cityTax"}
				item={item}
			/>
		</CardsHrm>
	)
}




export const TaxDecuctionItem  = ({ label, item, onChange, keyFld,  percentDefault }) => {
	const [edit, setEdit] = useState(0);
	const [value, setValue] = useState(0);


	
	const handleKey = (v) => {
		if(v.keyCode===13){
			if(isNotEmpty(value)){
				let nn = item?.[keyFld] || {};
				nn["percent"] = value;
				onChange && onChange(keyFld,nn);
			}
			setEdit(false);
		}
	};

	let percentLbl =  value || item?.[keyFld]?.percent || percentDefault;

	const handleAccPick = (v) => {
		let nn = item?.[keyFld] || {};
		nn["account"] = v;
		onChange && onChange(keyFld,nn);
	};

	const pick = () => {
		let dataModal = {};
		dataModal["zIndex"] = 450;
		dataModal["height"] = "80vh";
		// data['observeResize']=true;
		dataModal["props"] = { minHeight: "1vh", overlay: true };
		dataModal["content"] = 
			<AddExtraExpenses item={ item?.[keyFld]?.account} confirm={handleAccPick}/>
		OpenModal(dataModal);
	}

	
	let hasError = !item?.[keyFld]?.account?.account;


	return (
		<div className="_dsplFlx w_100prc">
			<div className="icon_revision" data-acc={`${item?.[keyFld]?.account?.account}`} >
				<Svg 
					name={hasError ? "alert":"check_circle"} 
					color={hasError ? "var(--hrm_palette-selected-nav-text)":"var(--hrm_palette-success-fg)"}
				/>
			</div>
			
			<div className="igLyC jyoObE pddBtm-12">
				<div className="txfnt_lfal" onClick={pick}>
				{label}
				</div>
			</div>
			
			<div className="igLyC jyoObE" onClick={()=>setEdit(!edit)}>{percentLbl+"%"}</div>
			
		</div>
	)
}



export const TaxExpensesItem  = ({ label, item, keyFld }) => {
	
	const [obs, set_obs] = useState(0);

	const updFrm = (f, v, k) => {
		let frmNm = "global_account_params"
		let frm2 = getStore(frmNm) || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}
		else{
			frm2[f] = v;
		}
		updStore(frmNm, frm2);
		set_obs(gen16CodeId())
	};


	
	let accountD = getStore("account_ids")?.[item?.[keyFld]?.account];

	const handleAccPick = (v) => {
		 v.account && updFrm && updFrm(keyFld,v);
		set_obs(gen16CodeId());
	};

	const pick = () => {
		let dataModal = {};
		dataModal["zIndex"] = 450;
		dataModal["height"] = "80vh";
		// data['observeResize']=true;
		dataModal["props"] = { minHeight: "1vh", overlay: true };
		dataModal["content"] = 
			<AddExtraExpenses item={item?.[keyFld]?.account} confirm={handleAccPick}/>
		OpenModal(dataModal);
	}

	
	let hasError = !accountD?.code;


	return (
		<div className="_dsplFlx w_100prc">
			<div className="icon_revision" data-acc={`${item?.[keyFld]?.account?.account}`} >
				<Svg 
					name={hasError ? "alert":"check_circle"} 
					color={hasError ? "var(--hrm_palette-selected-nav-text)":"var(--hrm_palette-success-fg)"}
				/>
			</div>
			
			<div className="igLyC jyoObE pddBtm-12">
				<div className="txfnt_lfal" style={{maxWidth:200}} onClick={pick}>
				{label}
				</div>
			</div>
			{accountD?.code ?
				<div className="igLyC jyoObE" >{accountD?.code}  - {accountD?.name}</div>
			:null}
		</div>
	)
}











