const AddExtraExpenses = ({data, confirm, item}) => {
	const [obs, set_obs] = useState(0);
	const [acc, setAcc] = useState({});
	let account = acc?.account || item?.account; 
	let sub_account = acc?.subAccount || item?.subAccount; 

	let accountD = getStore("account_ids")?.[account];
	let subaccountD = getStore("sub_account_ids")?.[sub_account];


	const addExtraAccount2List = () => {
		confirm && confirm(acc);
		CloseModal({id:data.modalID})
	}

	const handlePick = (acc,subacc) => {
		let nn= {
			"account":acc,
			"subAccount":subacc
		}
		setAcc(nn);
		set_obs(gen16CodeId())
	}


	
	
	const clear = () => {
		setAcc(null);
		set_obs(gen16CodeId())
	};

	return (
		<div style={{
			maxHeight: "80vh",
			minHeight: "60vh",
			width: "560px",
			overflow: "auto",
			background: "#fff",
			borderRadius: 13,
			padding: "10px 19px"
		}}>
			<div className="_dsplFlx ">
				<p className="form_title">
					Associar Cuenta
				</p>
				<div className="flexSpace"/>
				
			</div>
		{account ? 
			<div className="_dsplFlx " style={{margin: "18px 0"}}>
				<div className=" acc_dtls">
					<div className="_dsplFlx"   onClick={clear}>
						<div className="code" style={{color: "#646cff"}}>
							{accountD?.code} 
						</div>
						<div className="name">
							{accountD?.name}
						</div>
					</div>
					{subaccountD ?
						<div className="_dsplFlx"  style={{margin: "10px 0 0 18px"}}>
							<div className="code" >
								{subaccountD?.subAccount} 
							</div>
							<div className="name">
								{subaccountD?. subAccountName}
							</div>
						</div>
						:null}
					<div className="flexSpace"/>
				</div>
			</div> :
			<DropInput
				width={420}
				handleClick={handlePick}
			/>
			}
			<div className="card">
				<button onClick={addExtraAccount2List}>
					Associar
				</button>
			</div>
			</div>
	)
}





